@import url('https://fonts.googleapis.com/css?family=Inter:400,700&display=swap');

body {
  background: linear-gradient(120deg, #e0e7ff 0%, #f8fafc 100%);
  font-family: 'Inter', Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App-header-pro {
  background: #fff;
  border-radius: 22px;
  box-shadow: 0 4px 40px 0 rgba(68,86,136,0.09), 0 1.5px 6px rgba(68,86,136,0.08);
  padding: 48px 32px 36px 32px;
  max-width: 430px;
  width: 96vw;
  margin: 30px 0;
  text-align: center;
  position: relative;
}

.Pro-logo {
  height: 52px;
  margin-bottom: 16px;
  filter: drop-shadow(0 2px 8px #a3bffa60);
  animation: Pro-logo-spin infinite 7s linear;
}

@keyframes Pro-logo-spin {
  from { transform: rotate(0deg);}
  to { transform: rotate(360deg);}
}

.Pro-main h1 {
  font-size: 2.1em;
  font-weight: 800;
  color: #232946;
  margin-bottom: 2px;
  position: relative;
  letter-spacing: 0.5px;
  padding-bottom: 0.4em;
}

.Pro-main .accent-bar {
  display: block;
  height: 3px;
  width: 44px;
  background: linear-gradient(90deg,#6366f1 40%,#98c6f2 100%);
  margin: 7px auto 0 auto;
  border-radius: 3px;
  content: "";
}

.Pro-main h2 {
  color: #6366f1;
  font-weight: 500;
  margin-bottom: 14px;
  font-size: 1.2em;
}
